

































import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';
import MLabel from '@/components/atoms/MTypography/MLabel/MLabel.vue';
import MBody from '@/components/atoms/MTypography/MBody/MBody.vue';
import MLink from '@/components/atoms/MLink/MLink.vue';
import MIcon from '@/components/atoms/MIcon/MIcon.vue';

export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MInfoItem',
  components: {
    MLabel,
    MLink,
    MBody,
    MIcon
  },
  props: {
    labelText: {
      type: String,
      default: ''
    },
    bodyText: {
      type: String,
      default: ''
    },
    linkText: {
      type: String,
      default: ''
    },
    href: {
      type: String,
      default: ''
    },
    icon: {
      type: Boolean,
      default: false
    },
    bodies: {
      type: Array,
      default: () => []
    },
    bodySize: {
      type: String,
      default: 'medium',
      validator(bodySize) {
        return ['', 'x-small', 'small', 'medium', 'large', 'x-large'].includes(
          bodySize
        );
      }
    },
    bodyColor: {
      type: String,
      default: '',
      validator(bodySize) {
        return [
          '',
          'ink',
          'ink-light',
          'ink-lighter',
          'white',
          'indigo'
        ].includes(bodySize);
      }
    },
    bodyWeight: {
      type: String,
      default: '',
      validator(bodySize) {
        return ['', 'slim', 'slightly-thick', 'bold'].includes(bodySize);
      }
    }
  }
});
