




























import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';

export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MAlert',
  props: {
    type: {
      type: String,
      default: '',
      validator(type) {
        return ['', 'draft', 'pending', 'publish'].includes(type);
      }
    },
    description: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    list: {
      type: Array,
      default: () => []
    },
    listsize: {
      type: String,
      default: 'small'
    }
  },
  computed: {
    alertType() {
      return this.type ? `alert--${this.type}` : '';
    }
  }
});
